<script>
import chapters from "../assets/data/chapters";
import mixins from "../mixins/mixins";
export default {
	name: 'Chapters',
  data() {
    return {
      chapters: chapters,
      showChaptersIntro: true
    };
  },
  methods: {
    hideIntro: function() {
      // Create session storage item to keep intro hidden if closed by user
      this.showChaptersIntro = false;
      sessionStorage.setItem("showChaptersIntro", "false");
      window.scrollTo(0, 0);
    }
  },
  mixins: [mixins],
  created: function() {
		window.scrollTo(0, 0);
  },
  mounted: function() {
    // Get session storage item to check whether intro has been hidden
    var introState = sessionStorage.getItem("showChaptersIntro");
    const intro = document.getElementById("menu-intro");
    if (introState === "false") {
      intro.classList.add("hide");
      this.showChaptersIntro = false;
    }
  },
	computed: {
		convertDate: function (date) {
			return new Date(date).toDateString().substring(4);
		}
	}
};
</script>

