export default {
	methods: {
		hideOrMinimize: function(e) {
			var doIt = (next, prev, section, caret) => {
				// If next or previous section visible and is .section then all sections are showing
				if ((next && !next.classList.contains("hide") && next.classList.contains("section")) || (prev && !prev.classList.contains("hide") && prev.classList.contains("section"))) {
					var children = Array.from(section.children);
					var childrenLength = children.length;
					for (let i = 1; i < childrenLength; i++) {
						children[i].classList.toggle("hide");
					}
					// Toggle flipped caret
					caret.classList.toggle("caretFlip");
				} else {
					// If next or previous section not visible then only one .section is showing
					section.classList.add("hide");
					// Show menu
					var menu = document.getElementsByClassName("menu")[0];
					menu.classList.remove("hide");
					window.scrollTo(0, 0);
				}
			}
			// Cache main variables and other variables for each situation, then send variables to function
			var headerClicked = e.target.tagName === "HEADER";
			var textClicked = e.target.tagName === "P";
			var caretClicked = e.target.tagName === "IMG";
			if (headerClicked) {
				let next = e.target.parentNode.nextElementSibling;
				let prev = e.target.parentNode.previousElementSibling;
				let section = e.target.parentNode;
				let caret = e.target.children[0];
				doIt(next, prev, section, caret);
			} else if (textClicked) {
				let next = e.target.parentNode.parentNode.nextElementSibling;
				let prev = e.target.parentNode.parentNode.previousElementSibling;
				let section = e.target.parentNode.parentNode;
				let caret = e.target.previousElementSibling;
				doIt(next, prev, section, caret);
			} else if (caretClicked) {
				let next = e.target.parentNode.parentNode.nextElementSibling;
				let prev = e.target.parentNode.parentNode.previousElementSibling;
				let section = e.target.parentNode.parentNode;
				let caret = e.target;
				doIt(next, prev, section, caret);
			}
		},
		menuSelect: function(e) {
			// Hide menu
			var menu = document.getElementsByClassName("menu")[0];
			menu.classList.add("hide");
			// Cache text content of menu button clicked
			var itemClicked = e.target.textContent.trim().toString();
			// Cache array of sections and length of array
			var sections = Array.from(document.getElementsByClassName("section"));
			var secLength = sections.length;
			var unhideDescendents = (i) => {
				// In case a section list has been hidden by user, loop through descendents of section to show and remove 'hide' class if it has it
				var descendents = Array.from(sections[i].getElementsByTagName('*'));
				var descendentsLength = descendents.length;
				for (let i = 0; i < descendentsLength; i++) {
					// Do not perform function on .caret or .back classes
					if (descendents[i].classList.contains("hide") && !descendents[i].classList.contains("caret") && !descendents[i].classList.contains("back")) {
						descendents[i].classList.remove("hide");
					}
				}
			}
			// Loop through sections in this component. If section title is same as title of menu button clicked then show it, else hide it
			for (let i = 0; i < secLength; i++) {
				// Polyfill for .includes() method
				if (!String.prototype.includes) {
					String.prototype.includes = function (search, start) {
						'use strict';
						if (typeof start !== 'number') {
							start = 0;
						}

						if (start + search.length > this.length) {
							return false;
						} else {
							return this.indexOf(search, start) !== -1;
						}
					};
				}
				// Cache text content of each section header text
				var targetSectionText = sections[i].children[0].textContent.trim().toString();
				var back = Array.from(document.getElementsByClassName("back"));
				var backLength = back.length;
				var carets = Array.from(document.getElementsByClassName("caret"));
				var caretsLength = carets.length;
				// If section header text is the same as the menu button clicked then toggle/remove hide class to show section
				if (targetSectionText === itemClicked) {
					sections[i].classList.remove("hide");
					unhideDescendents(i);
					// If menu item clicked is for only one section then make sure back icon is showing for all sections
					for (let b = 0; b < backLength; b++) {
						if (back[b].classList.contains("hide")) {
							back[b].classList.remove("hide");
						}
					}
					// Hide carets (meant to display only when showing all sections)
					for (let c = 0; c < caretsLength; c++) {
						// Keep carets for youtube sections, chapter link and note sections
						if (!carets[c].parentNode.className.includes("yt", 0) && !carets[c].parentNode.nextElementSibling.className.includes("links", 0) && !carets[c].parentNode.nextElementSibling.className.includes("notes", 0)) {
							carets[c].classList.add("hide");
						}
					}
					// noteSectionAdjust();
				} else if (itemClicked === "SHOW ALL") {
					// If user clicks Show All button then remove hide class on all component sections
					sections[i].classList.remove("hide");
					// If menu item clicked is to show all sections then hide back icons
					for (let b = 0; b < backLength; b++) {
						back[b].classList.add("hide");
					}
					// Show carets (because all sections showing and header will hide content)
					for (let c = 0; c < caretsLength; c++) {
						carets[c].classList.remove("hide");
						if (carets[c].classList.contains("caretFlip")) {
							carets[c].classList.remove("caretFlip");
						}
					}
					// noteSectionAdjust();
					unhideDescendents(i);
				}
			}
			window.scrollTo(0, 0);
		},
		toggleSectionContent: function(e) {
			// Hide section content after header
			var hideSiblings = (siblings) => {
				var sibLength = siblings.length;
				for (let i = 1; i < sibLength; i++) {
					siblings[i].classList.toggle("hide");
				}
			}
			// Cache variables
			var headerClicked = e.target.tagName === "HEADER";
			var textClicked = e.target.tagName === "P";
			var caretClicked = e.target.classList.contains("caret");
			// Determine which element clicked and pass siblings variable to hideSiblings function
			if (headerClicked) {
				e.target.children[0].classList.toggle("caretFlip");
				let siblings = e.target.parentNode.children;
				hideSiblings(siblings);
			} else if (textClicked) {
				e.target.previousElementSibling.classList.toggle("caretFlip");
				let siblings = e.target.parentNode.parentNode.children;
				hideSiblings(siblings);
			} else if (caretClicked) {
				e.target.classList.toggle("caretFlip");
				let siblings = e.target.parentNode.parentNode.children;
				hideSiblings(siblings);
			}
		}
	}
}
