import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Chapters from './views/Chapters.vue'
import Media from './views/Media.vue'
import Tools from './views/Tools.vue'
import Quotes from './views/Quotes.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { 
			path: '/',
			name: 'Home', 
			component: Home
		},
		{ 
			path: '/chapters',
			name: 'Chapters', 
			component: Chapters
		},
		{ 
			path: '/media',
			name: 'Media', 
			component: Media
		},
		{ 
			path: '/tools',
			name: 'Tools', 
			component: Tools
		},
		{ 
			path: '/quotes',
			name: 'Quotes', 
			component: Quotes
		},
		{ 
			path: '*', 
			redirect: '/',
			name: 'NotFound',
      component: Home
		}
  ]
})
