<template>
	<div id="media-wrapper">
		<!-- MENU -->
		<menu class="menu">
			<header class="menu-header">Media</header>
			<!-- INTRO TEXT -->
			<aside v-if="showMediaIntro" @click="hideIntro" id="menu-intro">
				<span id="menu-intro-hide">X</span>
				<p id="menu-intro-text">
					This section contains curated collections of some of history's greatest myths/stories, non-fictional works, and other media which offer invaluable wisdom, information, and practical knowledge / guidance related to various obstacles we face from the personal level to the global. Myths, simulations, metaphorical and symbolic works often enable us to learn vicariously, communicate transcendent truths, and transform our experience and understanding of life.
				</p>
			</aside>
			<ul class="menu-list">
				<li v-for="(section, i) in media" @click="menuSelect" class="menu-list-item" v-text="section.title" :key="i"></li>
				<li @click="menuSelect" class="menu-list-item">SHOW ALL</li>
			</ul>
		</menu>
		<!-- MEDIA SECTIONS -->
		<section v-for="(section, i) in media" class="section hide" :key="i">
			<header @click="hideOrMinimize" class="section-header">
				<img src="../assets/img/other/caret.svg" alt="caret" class="caret">
				<p class="section-header-text" v-text="section.title"></p>
				<img src="../assets/img/other/back.svg" alt="back" class="back hide">
			</header>
			<!-- SORT SECTION -->
			<aside class="section-sort" v-if="section.sortable">
				<p class="section-sort-header">Sort</p>
				<ul class="section-sort-list hide" @click="sortItems">
					<li class="section-sort-list-option" data-sort="title descending">Title 
						<img src="../assets/img/other/downArrow.svg" alt="descending order" class="section-sort-list-option-arrow" data-sort="title descending">
					</li>
					<li class="section-sort-list-option" data-sort="title ascending">Title 
						<img src="../assets/img/other/upArrow.svg" alt="ascending order" class="section-sort-list-option-arrow" data-sort="title ascending">
					</li>
					<li class="section-sort-list-option" data-sort="year descending">Year 
						<img src="../assets/img/other/downArrow.svg" alt="descending order" class="section-sort-list-option-arrow" data-sort="year descending">
					</li>
					<li class="section-sort-list-option" data-sort="year ascending">Year 
						<img src="../assets/img/other/upArrow.svg" alt="ascending order" class="section-sort-list-option-arrow" data-sort="year ascending">
					</li>
					<li class="section-sort-list-option" data-sort="rating descending">Rating 
						<img src="../assets/img/other/downArrow.svg" alt="descending order" class="section-sort-list-option-arrow" data-sort="rating descending">
					</li>
					<li class="section-sort-list-option" data-sort="rating ascending">Rating 
						<img src="../assets/img/other/upArrow.svg" alt="ascending order" class="section-sort-list-option-arrow" data-sort="rating ascending">
					</li>
					<li class="section-sort-list-option" data-sort="original">Original Order</li>
				</ul>
			</aside>
			<!-- ALL SECTIONS EXCEPT YOUTUBE -->
			<ul v-if="section.list" class="section-list">
				<li v-for="(item, i) in section.list" :key="i" :data-title="item.title" :data-year="item.year" :data-index="i" class="section-list-item">
					<!-- VIMEO DATA ONLY CONTAINS LINK TO VIDEO SO USE ANCHOR TAG -->
					<a v-if="item.link" :href="item.link" class="section-list-item-title" target="_blank">{{item.title}} <span v-if="item.year" class="section-list-item-year" v-text="'(' + item.year + ')'"></span></a>
					<!-- NON-VIMEO ITEMS BELOW -->
					<header v-if="item.links" class="section-list-item-title">{{item.title}} <span v-if="item.year" class="section-list-item-year" v-text="'(' + item.year + ')'"></span><span v-if="item.author" v-text="'- ' + item.author"></span></header>
					<a v-if="item.info" :href="item.link" class="section-list-item-plot" target="_blank">{{item.info}}</a>
					<p v-if="item.plot" class="section-list-item-plot">{{item.plot}}</p>
					<!-- RATINGS -->
					<p v-if="item.imdbRating" class="section-list-item-rating" :data-rating="item.imdbRating" data-ratetype="imdb">
						{{item.imdbRating}} / 10
						<img src="../assets/img/other/star.svg" alt="star rating" class="star">
						<a class="section-list-item-rating-source" href="http://www.imdb.com/" target="_blank">(IMDb)</a>
					</p>
					<p v-if="item.goodreadsRating" class="section-list-item-rating" :data-rating="item.goodreadsRating" data-ratetype="goodreads">
						{{item.goodreadsRating}} / 5
						<img src="../assets/img/other/star.svg" alt="star rating" class="star">
						<a class="section-list-item-rating-source" href="https://www.goodreads.com/" target="_blank">(Goodreads)</a>
					</p>
					<p v-if="item.metacriticRating" class="section-list-item-rating" :data-rating="item.metacriticRating" data-ratetype="metacritic">
						{{item.metacriticRating}} / 100
						<img src="../assets/img/other/star.svg" alt="star rating" class="star">
						<a class="section-list-item-rating-source" href="http://www.metacritic.com/" target="_blank">(Metacritic)</a>
					</p>
					<!-- LINKS -->
					<footer v-if="item.links" class="section-list-item-links">
						<a v-for="(link, i) in item.links" v-if="link.link" :key="i" :href="link.link" target="_blank" class="section-list-item-link">
							<p class="section-list-item-link-text" v-text="link.title"></p>
						</a>
					</footer>
				</li>
			</ul>
			<!-- YOUTUBE CHANNELS -->
			<section v-if="section.channels" v-for="(channel, i) in section.channels" class="yt-channel" :key="i">
				<header @click="toggleSectionContent" class="yt-channel-header">
					<img src="../assets/img/other/caret.svg" alt="caret" class="caret">
					<p v-text="channel.title" class="yt-channel-header-text"></p>
				</header>
				<div class="yt-channel-description">
					<p class="yt-channel-description-text" v-text="channel.description"></p>
					<a class="yt-channel-description-site" :href="channel.site" target="_blank">Official Site Link</a>
					</div>
				<article v-if="channel.list" class="yt-channel-list">
					<a v-for="(video, i) in channel.list" target="_blank" :href="video.link" class="yt-channel-list-item" :key="i">
						<p v-text="video.title" class="yt-channel-list-item-title"></p>
						<p v-if="video.tedSpeaker || video.taSpeaker" v-text="video.tedSpeaker || video.taSpeaker" class="yt-channel-list-item-speaker"></p>
					</a>
				</article>
				<!-- ROGAN PODCAST LIST. DIFFERENT STYLE SECTION TO INCLUDE DESCRIPTIONS AND HAVE FEWER PER ROW -->
				<article v-if="channel.roganList" class="yt-channel-rogan-list">
					<a v-for="(video, i) in channel.roganList" target="_blank" :href="video.link" class="yt-channel-rogan-list-item" :key="i">
						<p v-text="video.title" class="yt-channel-rogan-list-item-title"></p>
						<p v-text="video.info" class="yt-channel-rogan-list-item-info"></p>
					</a>
				</article>
				<!-- CHANNEL SUB SECTIONS -->
				<section v-if="channel.subSections" v-for="(section, i) in channel.subSections" class="yt-subsection" :key="i">
					<header @click="toggleSectionContent" class="yt-subsection-header">
						<img src="../assets/img/other/caret.svg" alt="caret" class="caret">
						<p v-text="section.title" class="yt-subsection-header-text"></p>
					</header>
					<article class="yt-subsection-list">
						<a v-for="(video, i) in section.list" :href="video.link"  target="_blank" class="yt-subsection-list-item" :key="i">
							<p v-text="video.title" class="yt-subsection-list-item-title"></p>
						</a>
					</article>
				</section>
				<!-- ROGAN PODCAST SUB SECTIONS. DIFFERENT STYLE SECTION TO INCLUDE DESCRIPTIONS AND HAVE FEWER PER ROW -->
				<section v-if="channel.roganSubSections" v-for="(section, i) in channel.roganSubSections" class="yt-subsection" :key="i">
					<header @click="toggleSectionContent" class="yt-subsection-header">
						<img src="../assets/img/other/caret.svg" alt="caret" class="caret">
						<p v-text="section.title" class="yt-subsection-header-text"></p>
					</header>
					<p v-text="section.info" class="yt-rogan-subsection-list-item-info"></p>
					<article class="yt-rogan-subsection-list">
						<a v-for="(video, i) in section.list" :href="video.link"  target="_blank" class="yt-rogan-subsection-list-item" :key="i">
							<p v-text="video.title" class="yt-rogan-subsection-list-item-title"></p>
						</a>
					</article>
				</section>
			</section>
		</section>
	</div>
</template>

<script>
import media from "../assets/data/media";
import mixins from "../mixins/mixins";
export default {
	name: 'Media',
  data() {
    return {
      media: media,
      showMediaIntro: true
    };
  },
  methods: {
    hideIntro: function() {
      // Create session storage item to keep intro hidden if closed by user
      this.showMediaIntro = false;
      sessionStorage.setItem("showMediaIntro", "false");
      window.scrollTo(0, 0);
    },
    sortItems: function(e) {
      // Function to append items after sorting
      function appendItems(list, listItems, listItemsLength) {
        for (let i = 0; i < listItemsLength; i++) {
          list.appendChild(listItems[i]);
        }
      }
      // Function to sort list then send to appendItems function
      function sortList(e, list, listItems, listItemsLength) {
        if (e.target.dataset.sort.toString() === "title descending") {
          function sortByTitle(a, b) {
            return a.dataset.title.toString() < b.dataset.title.toString()
              ? -1
              : a.dataset.title.toString() > b.dataset.title.toString() ? 1 : 0;
          }
          listItems.sort(sortByTitle);
          appendItems(list, listItems, listItemsLength);
          // Sort by Title (Ascending)
        } else if (e.target.dataset.sort.toString() === "title ascending") {
          function sortByTitleReverse(a, b) {
            return b.dataset.title.toString() < a.dataset.title.toString()
              ? -1
              : b.dataset.title.toString() > a.dataset.title.toString() ? 1 : 0;
          }
          listItems.sort(sortByTitleReverse);
          appendItems(list, listItems, listItemsLength);
          // Sort by Year (Descending)
        } else if (e.target.dataset.sort.toString() === "year descending") {
          function sortByYear(a, b) {
            return parseInt(b.dataset.year) < parseInt(a.dataset.year)
              ? -1
              : parseInt(b.dataset.year) > parseInt(a.dataset.year) ? 1 : 0;
          }
          listItems.sort(sortByYear);
          appendItems(list, listItems, listItemsLength);
          // Sort by Year (Ascending)
        } else if (e.target.dataset.sort.toString() === "year ascending") {
          function sortByYearReverse(a, b) {
            return parseInt(a.dataset.year) < parseInt(b.dataset.year)
              ? -1
              : parseInt(a.dataset.year) > parseInt(b.dataset.year) ? 1 : 0;
          }
          listItems.sort(sortByYearReverse);
          appendItems(list, listItems, listItemsLength);
          // Sort by Rating (Descending)
        } else if (e.target.dataset.sort.toString() === "rating descending") {
          function sortByRating(a, b) {
            return b.children[2].dataset.rating.toString() <
              a.children[2].dataset.rating.toString()
              ? -1
              : b.children[2].dataset.rating.toString() >
                a.children[2].dataset.rating.toString()
                ? 1
                : 0;
          }
          listItems.sort(sortByRating);
          appendItems(list, listItems, listItemsLength);
          // Sort by Rating (Ascending)
        } else if (e.target.dataset.sort.toString() === "rating ascending") {
          function sortByRatingReverse(a, b) {
            return a.children[2].dataset.rating.toString() <
              b.children[2].dataset.rating.toString()
              ? -1
              : a.children[2].dataset.rating.toString() >
                b.children[2].dataset.rating.toString()
                ? 1
                : 0;
          }
          listItems.sort(sortByRatingReverse);
          appendItems(list, listItems, listItemsLength);
          // Sort by Original Order
        } else if (e.target.dataset.sort.toString() === "original") {
          function sortByIndex(a, b) {
            return parseInt(a.dataset.index) < parseInt(b.dataset.index)
              ? -1
              : parseInt(a.dataset.index) > parseInt(b.dataset.index) ? 1 : 0;
          }
          listItems.sort(sortByIndex);
          appendItems(list, listItems, listItemsLength);
        }
      };
      // Determine and cache variables then send to sortList function
      if (e.target.tagName === "LI") {
        let list = e.target.parentNode.parentNode.nextElementSibling;
        let listItems = Array.from(
          e.target.parentNode.parentNode.nextElementSibling.children
        );
        let listItemsLength = listItems.length;
        sortList(e, list, listItems, listItemsLength);
      } else if (e.target.tagName === "IMG") {
        let list = e.target.parentNode.parentNode.parentNode.nextElementSibling;
        let listItems = Array.from(
          e.target.parentNode.parentNode.parentNode.nextElementSibling.children
        );
        let listItemsLength = listItems.length;
        sortList(e, list, listItems, listItemsLength);
      }
    }
  },
  mixins: [mixins],
  created: function() {
		window.scrollTo(0, 0);
  },
  mounted: function() {
    // Get session storage item to check whether intro has been hidden
    var introState = sessionStorage.getItem("showMediaIntro");
    const intro = document.getElementById("menu-intro");
    if (introState === "false") {
      intro.classList.add("hide");
      this.showChaptersIntro = false;
    }
  }
};
</script>

<style lang="sass" scoped>
	@import '../mixins/_variables'
	@import '../mixins/_mixins'
	*
		padding: 0
		margin: 0
		box-sizing: border-box
		line-height: 1em
	.hide
		display: none !important
	.caretFlip
		animation: caretFlip 0.25s forwards ease
	@keyframes caretFlip
		0%
			transform: rotate(0deg)
		100%
			transform: rotate(180deg)
	@keyframes fadeIn 
		0%
			opacity: 0
		100%
			opacity: 1
	#media-wrapper
		max-width: 80em
		min-height: 91vh
		animation: $fadeIn
		.menu
			@include border
			margin: 3em 0.25em 2em 0.25em
			background-color: $sectionBG
			animation: $fadeIn
			transition: $transition
			border-radius: $box-border-radius
			box-shadow: $section-inner-box-shadow
			@media (min-width: 35em)
				margin: 3em 0.5em 2em 0.5em
			@media (min-width: 80em)
				margin: 3em 0 2em 0
			.menu-header
				font-size: 1.618em
				padding: 0.5em
				text-align: center
				cursor: pointer
				border-radius: 0.5em 0.5em 0 0
				text-shadow: $text-shadow
				background-color: $maintitle
				background: linear-gradient(lighten($maintitle, 10%), darken($maintitle, 10%))
			#menu-intro
				@include border
				position: relative
				margin: 1em 0.25em
				cursor: pointer
				max-width: 70em
				transition: $transition
				box-shadow: $box-shadow
				@media (min-width: 35em)
					margin: 1em
				@media (min-width: 74em)
					margin: 1em auto
				#menu-intro-hide
					padding: 0.25em
					text-align: right
					position: absolute
					top: 0
					right: 0
				#menu-intro-text
					padding: 1.1em
					border-radius: $box-border-radius
					text-indent: 0.5em
					line-height: 1.15em
					text-shadow: $text-shadow
					transition: $transition
					box-shadow: $section-inner-box-shadow
					background-color: $subsectionBG
					background: linear-gradient(lighten($subsectionBG, 2.5%), darken($subsectionBG, 2.5%))
					@media (min-width: 45em)
						font-size: 1.1em
			.menu-list
				list-style-type: none
				padding: 1em 0.5em
				display: flex
				flex-wrap: wrap
				justify-content: space-around
				transition: $transition
				@media (min-width: 25em)
					padding: 1em
				.menu-list-item
					border-radius: 0.25em
					border: 0.125em solid rgba(255, 255, 255, 0)
					@include flexCenter
					flex-grow: 1
					margin: 0.25em
					width: 95%
					@media (min-width: 25em)
						width: 45%
					@media (min-width: 45em)
						width: 30%
					text-align: center
					padding: 0.5em
					font-size: 1.05em
					cursor: pointer
					box-shadow: $box-shadow, $listitem-inner-box-shadow
					text-shadow: $text-shadow
					transition: $transition
					background-color: $listitem
					background: linear-gradient(lighten($listitem, 7.5%), darken($listitem, 7.5%))
					&:hover
						border: 0.125em solid rgba(255, 255, 255, 0.75)
					@media (min-width: 45em)
						font-size: 1.25em
						padding: 1em
		.section
			@include border
			margin: 3em 0.25em 0 0.25em
			background-color: $sectionBG
			animation: $fadeIn
			transition: $transition
			border-radius: $box-border-radius
			box-shadow: $section-inner-box-shadow
			@media (min-width: 35em)
				margin: 3em 0.5em 2em 0.5em
			@media (min-width: 80em)
				margin: 3em 0 2em 0
			.section-header
				@include flexCenter
				position: sticky
				top: 1.35em
				z-index: 2
				font-size: 1.618em
				padding: 0.5em
				text-align: center
				cursor: pointer
				border-radius: 0.5em 0.5em 0 0
				text-shadow: $text-shadow
				box-shadow: $box-shadow
				background-color: $maintitle
				background: linear-gradient(lighten($maintitle, 10%), darken($maintitle, 10%))
				.section-header-text
					max-width: 75%
				.back, .caret
					height: 1em
					color: white
					position: absolute
					left: 0
					margin-left: 0.5em
			.section-sort
				@include border
				margin: 2em 0.5em 1em 0.5em
				background-color: $sectionBG2
				transition: $transition
				box-shadow: $box-shadow
				border-radius: $box-border-radius
				@media (min-width: 40em)
					max-width: 85%
					margin: 2em auto 1em auto
				.section-sort-header
					@include flexCenter
					background-color: $sectiontitle
					background: linear-gradient(lighten($sectiontitle, 10%), darken($sectiontitle, 10%))
					padding: 0.5em
					border-radius: 0.5em 0.5em 0 0
					font-size: 1.1em
					text-shadow: $text-shadow
				.section-sort-list
					display: flex
					flex-wrap: wrap
					justify-content: space-around
					padding: 0.25em
					list-style-type: none
					margin: 0 auto
					transition: $transition
					background-color: $sectionBG2
					box-shadow: $section-inner-box-shadow
					@media (min-width: 28em)
						padding: 0.5em
					.section-sort-list-option
						border: 0.125em solid rgba(255, 255, 255, 0)
						border-radius: 0.25em
						@include flexCenter
						width: 40%
						flex-grow: 1
						text-align: center
						margin: 0.25em
						@media (min-width: 28em)
							width: 30%
						@media (min-width: 60em)
							width: 10%
						padding: 0.5em
						cursor: pointer
						background-color: $listitem
						background: linear-gradient(lighten($listitem, 7.5%), darken($listitem, 7.5%))
						transition: $transition
						text-shadow: $text-shadow
						&:hover
							border: 0.125em solid rgba(255, 255, 255, 0.75)
						.section-sort-list-option-arrow
							height: 1em
			.section-list
				text-decoration: none
				list-style-type: none
				display: flex
				flex-wrap: wrap
				justify-content: space-around
				align-items: stretch
				transition: $transition
				@media (min-width: 25em)
					padding: 0.5em
				.section-list-item
					@include border
					box-shadow: $box-shadow
					background-color: $listitem
					width: 95%
					flex-grow: 1
					margin: 0.5em
					display: flex
					flex-direction: column
					transition: $transition
					animation: fadeIn 0.2s forwards ease-in-out
					@media (min-width: 48em)
						width: 45%
					&:hover
						border: 0.125em solid rgba(255, 255, 255, 0.75)
					.section-list-item-title
						padding: 0.5em
						font-size: 1.25em
						text-decoration: none
						text-align: center
						cursor: pointer
						line-height: 1.1em
						text-shadow: $text-shadow
						transition: $transition
						background-color: $listitem
						box-shadow: $listitem-inner-box-shadow
						background: linear-gradient(lighten($listitem, 10%), darken($listitem, 10%))
						.section-list-item-year
							padding: 0em 0.25em 0em 0em
					.section-list-item-plot
						@include flexCenter
						flex-grow: 1
						display: flex
						justify-content: center
						text-align: left
						text-decoration: none
						text-indent: 0.5em
						padding: 0.5em 0.5em 1em 0.5em
						line-height: 1em
						transition: $transition
						text-shadow: $text-shadow
						box-shadow: $section-inner-box-shadow
						background-color: $subsubsectiontitle
						background: linear-gradient(lighten($subsubsectiontitle, 2.5%), darken($subsubsectiontitle, 2.5%))
						@media (min-width: 25em)
							padding: 0.75em 0.75em 1em 0.75em
					.section-list-item-rating
						@include flexCenter
						text-shadow: $text-shadow
						text-align: right
						padding: 0.25em
						background-color: $subsubsectiontitle
						background: linear-gradient(lighten($subsubsectiontitle, 2.5%), darken($subsubsectiontitle, 2.5%))
						.section-list-item-rating-source
							text-decoration: none
							font-size: 0.75em
							&:hover
								text-decoration: underline
						.star
							height: 1em
							width: 1em
							margin: 0 0.5em 0 0
					.section-list-item-links
						display: flex
						flex-basis: auto
						flex-wrap: wrap
						.section-list-item-link
							text-align: center
							padding: 0.5em
							flex-grow: 1
							text-decoration: none
							border: 0.1em solid rgba(255, 255, 255, 0.1)
							transition: $transition
							font-size: 0.85em
							text-shadow: $text-shadow
							box-shadow: $listitem-inner-box-shadow
							background-color: $subsectiontitle
							&:hover
								color: white
								background-color: $hover
								border: 0.1em solid rgba(255, 255, 255, 0.25)
								.section-list-item-link-text
									opacity: 1
							.section-list-item-link-text
								opacity: 0.75
			.yt-channel
				@include border
				margin: 1em 0.25em 2em 0.25em
				background-color: $sectionBG2
				transition: $transition
				border-radius: $box-border-radius
