<template>
	<section id="navigation">
		<ul id="nav-list">
			<li class="nav-list-li active" id="home">
				<router-link to="/" class="nav-list-item">
					<img src="../assets/img/nav-icons/home.svg" alt="home" class="nav-icon home">
					<p class="nav-text">Home</p>
				</router-link>
			</li>
			<li @click="showmenu" class="nav-list-li" id="chapters">
				<router-link to="/chapters" class="nav-list-item">
					<img src="../assets/img/nav-icons/chapters.svg" alt="chapters" class="nav-icon">
					<p class="nav-text">Chapters</p>
				</router-link>
			</li>
			<li @click="showmenu" class="nav-list-li" id="media">
				<router-link to="/media" class="nav-list-item">
					<img src="../assets/img/nav-icons/media.svg" alt="media" class="nav-icon">
					<p class="nav-text">Media</p>
				</router-link>
			</li>
			<li @click="showmenu" class="nav-list-li" id="tools">
				<router-link to="/tools" class="nav-list-item">
					<img src="../assets/img/nav-icons/tools.svg" alt="tools" class="nav-icon">
					<p class="nav-text">Tools</p>
				</router-link>
			</li>
			<li @click="showmenu" class="nav-list-li" id="quotes">
				<router-link to="/quotes" class="nav-list-item">
					<img src="../assets/img/nav-icons/quotes.svg" alt="quotes" class="nav-icon">
					<p class="nav-text">Quotes</p>
				</router-link>
			</li>
		</ul>
	</section>
</template>

<script>
export default {
  methods: {
		showmenu: function () {
			var sections = [...document.getElementsByClassName('section')];
			var secLen = sections.length;
			for (var i = 0; i < secLen; i++) {
				sections[i].classList.add('hide');
			}
			var menu = document.getElementsByClassName('menu')[0];
			menu.classList.remove('hide');
			window.scrollTo(0, 0)
		}
	}
};
</script>

<style lang="sass" scoped>
	@import '../mixins/_variables'
	@import '../mixins/_mixins'
	*
		padding: 0
		margin: 0
		box-sizing: border-box
		line-height: 1em
	@keyframes fadeIn 
		0%
			opacity: 0
		100%
			opacity: 1
	// .active
	// 	background-color: lighten($listitem, 12.5%)
	// 	box-shadow: $box-shadow, $listitem-inner-box-shadow
	#navigation
		z-index: 1
		position: fixed
		#nav-list
			top: 0
			width: 100%
			padding: 0
			margin: 0
			display: flex
			position: fixed
			align-items: center
			height: 2.2em
			max-width: 80em
			list-style-type: none
			border-bottom: $border
			border-radius: 0 0 0.1875em 0.1875em
			background-color: $listitem
			background: linear-gradient(lighten($listitem, 7.5%), darken($listitem, 7.5%))
			@media (min-width: 80em)
				border: $border
				border-top: none
			li
				flex: 1
			.nav-list-item
				@include flexCenter
				color: white
				padding: 0.25em
				margin: 0
				font-size: 1.05em
				text-align: center
				text-decoration: none
				transition: $transition
				text-shadow: $text-shadow
				&:hover
					cursor: pointer
					background-color: lighten($listitem, 12.5%)
					text-shadow: 0em 0em 1em rgba(0, 0, 0, 0.5)
				&.router-link-exact-active
					background-color: lighten($listitem, 12.5%)
					box-shadow: $box-shadow, $listitem-inner-box-shadow
				@media (max-width: 35em)
					font-size: 0.8em
				.nav-icon
					padding: 0 0.5em 0 0
					@media (max-width: 35em)
						padding: 0.1em
				.nav-text
					transition: display 0.3s
					text-transform: uppercase
					@media (max-width: 35em)
						display: none
				.home
					padding: 0 0.5em 0 0
					@media (max-width: 35em)
						padding: 0.1em
</style>
