<template>
	<div id="quotes-wrapper">
		<!-- MENU -->
		<menu class="menu">
			<header class="menu-header">Quotes</header>
			<!-- INTRO TEXT -->
			<aside v-if="showIntro" @click="hideIntro" id="menu-intro">
				<span id="menu-intro-hide">X</span>
				<p id="menu-intro-text">
					This section includes curated collections of quotes by some of history's greatest thinkers and other extraordinary people. The quotes are not replacements for the books and bodies of work which they have been drawn from, but rather are intended to be condensed essences which offer some brief practical insights as well as a kind of preview of the person's work and what they are/were about. It may be useful to think of these concise bits of wisdom as strings of code which, when analyzed and integrated with one's personal 'code / software / operating system', can significantly upgrade one's understanding.
				</p>
			</aside>
			<ul class="menu-list">
				<div class="sub-menu">
					<p class="sub-menu-title">Topics</p>
					<div class="sub-menu-list">
						<li v-for="(section, i) in quotes.topics" @click="menuSelect" class="menu-list-item" v-text="section.title" :key="i"></li>
					</div>		
				</div>
				<div class="sub-menu">	
					<p class="sub-menu-title">People</p>	
					<div class="sub-menu-list">
						<li v-for="(section, e) in quotes.people" @click="menuSelect" class="menu-list-item" v-text="section.name" :key="e"></li>
					</div>		
				</div>
				<li @click="menuSelect" class="menu-list-item all">SHOW ALL</li>
			</ul>
		</menu>
		<!-- QUOTE SECTIONS -->
		<!-- TOPICS -->
		<section v-for="(section, i) in quotes.topics" class="section hide" :key="i">
			<header @click="hideOrMinimize" class="section-header">
				<img src="../assets/img/other/caret.svg" alt="caret" class="caret hide">
				<p class="section-header-text" v-text="section.title"></p>
				<img src="../assets/img/other/back.svg" alt="back" class="back hide">
			</header>
			<!-- QUOTES -->
			<ul v-if="section.quotes" class="section-quote-list">
				<li v-for="(quote, i) in section.quotes" class="section-quote-list-quote topics" :key="i">
					<p class="section-quote-list-quote-text" v-text="quote.quote"></p>
					<a :href="quote.link" class="section-quote-list-quote-author" target="_blank">
						<p class="section-quote-list-quote-author-text" v-text="quote.author"></p>
					</a>
				</li>
			</ul>
		</section>
		<!-- PEOPLE -->
		<section v-for="(section, i) in quotes.people" class="section hide" :key="i">
			<header @click="hideOrMinimize" class="section-header">
				<img src="../assets/img/other/caret.svg" alt="caret" class="caret hide">
				<p class="section-header-text" v-text="section.name"></p>
				<img src="../assets/img/other/back.svg" alt="back" class="back hide">
			</header>
			<!-- PERSON INFO -->
			<section v-if="section.bio" class="section-info">
				<article class="section-info-content">
					<p class="section-info-content-bio" v-text="section.bio"></p>
					<!-- WIKI LINKS -->
					<section class="section-info-content-links">
						<a v-if="section.wiki" class="section-info-content-link" :href="section.wiki" target="_blank">Wikipedia</a>
						<a v-if="section.wikiquote" class="section-info-content-link" :href="section.wikiquote" target="_blank">Wikiquote</a>
						<a v-if="section.wikisource" class="section-info-content-link" :href="section.wikisource" target="_blank">Wikisource</a>
					</section>
					<!-- OTHER LINKS -->
					<section v-if="section.links" class="section-info-content-links">
						<a v-for="(item, i) in section.links" :key="i" :href="item.link" class="section-info-content-link" target="_blank" v-text="item.title"></a>
					</section>
					<!-- BOOKS -->
					<section v-if="section.books" class="section-info-content-books">
						<header class="section-info-content-books-header">Popular Books</header>
						<ul class="section-info-content-books-list">
							<li v-for="(book, i) in section.books" class="section-info-content-book" :key="i">
								<header class="section-info-content-book-title">{{book.title}} <span v-if="book.year" class="section-info-content-book-year">({{book.year}})</span></header>
								<section v-if="book.links" class="section-info-content-book-links">
									<a v-for="(link, i) in book.links" :href="link.link" :key="i" target="_blank" class="section-info-content-book-link">
										<p class="section-info-content-book-link-text" v-text="link.title"></p>
									</a>
								</section>
							</li>
						</ul>
					</section>
				</article>
			</section>
			<!-- QUOTES -->
			<ul v-if="section.quotes" class="section-quote-list">
				<li v-for="(quote, i) in section.quotes" class="section-quote-list-quote" :key="i">
					<p class="section-quote-list-quote-text" v-text="quote"></p>
				</li>
			</ul>
			<!-- MIXED QUOTES -->
			<ul v-if="section.mixedQuotes" class="section-quote-list-mixed">
				<li v-for="(item, i) in section.mixedQuotes" :key="i" class="section-quote-list-mixed-quote">
					<p class="section-quote-list-mixed-quote-text" v-text="item.quote"></p>
					<a :href="item.link" class="section-quote-list-mixed-quote-name" target="_blank">
						<p class="section-quote-list-mixed-quote-name-text" v-text="item.name"></p>
					</a>
				</li>
			</ul>
		</section>
	</div>
</template>

<script>
import quotes from "../assets/data/quotes";
import mixins from "../mixins/mixins";
export default {
	name: 'Quotes',
  data() {
    return {
      quotes: quotes,
      showIntro: true
    };
  },
  methods: {
    hideIntro: function() {
      // Create session storage item to keep intro hidden if closed by user
      this.showIntro = false;
      sessionStorage.setItem("showQuotesIntro", "false");
      window.scrollTo(0, 0);
    }
  },
  mixins: [mixins],
  created: function() {
		window.scrollTo(0, 0);
  },
  mounted: function() {
    // Get session storage item to check whether intro has been hidden
    var introState = sessionStorage.getItem("showQuotesIntro");
    const intro = document.getElementById("menu-intro");
    if (introState === "false") {
      intro.classList.add("hide");
      this.showIntro = false;
    }
  }
};
</script>
	
